.review {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0;
  width: 100%;
  max-width: 100%;
  color: $primary-color;
  box-sizing: border-box;

  &__pic {
    position: relative;
    width: 30%;
    height: 100%;
    box-sizing: border-box;

    @media (max-width: $table) {
      width: 100%;
      max-width: 350px;
      align-items: center;
    }

    &--mask {
      position: relative;

      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        max-height: 528px;
        background-size: 100% 100%;
        background-repeat: no-repeat;
        background-image: url("../img/reviews/review-mask.png");

        overflow: hidden;
      }
    }

    & img {
      width: 100%;
      height: 100%;
      max-width: 98%;
      max-height: 98%;
      margin: 2px;
      object-fit: contain;
    }
  }

  &__program {
    margin-bottom: 32px;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 140%;

    color: $primary-color;
    opacity: 0.74;

    & span {
      color: $secondary-color;
    }
  }

  &__info {
    margin: 0;
    padding-top: 33px;
    width: 66%;

    display: flex;
    flex-direction: column;

    box-sizing: border-box;

    @media (max-width: $table) {
      width: 100%;
      align-items: center;
    }

    & h3 {
      margin: 0;
      margin-bottom: 10px;

      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      line-height: 150%;

      color: $primary-color;
      opacity: 0.74;

      @media (max-width: $table) {
        margin-left: 102px;
        margin-bottom: 23px;
      }

      @media (max-width: $mobile) {
        margin-left: 0;
      }
    }

    & p {
      margin: 0;
      margin-bottom: 20px;
      width: 100%;

      font-style: normal;
      font-weight: normal;
      font-size: 17px;
      line-height: 170%;

      color: $primary-color;
      opacity: 0.74;

      @media (max-width: $table) {
        text-align: left;
      }

      @media (max-width: $mobile) {
        text-align: left;
        font-size: 16px;
        line-height: 160%;
      }
    }
  }

  &__text {
    padding-right: 73px;

    @media (max-width: $mobile) {
      padding-right: 0;
    }

    & a {
      font-style: normal;
      font-weight: normal;
      font-size: 17px;
      line-height: 150%;

      text-decoration-line: underline;

      color: $secondary-color;
    }
  }

  &__social {
    display: flex;
    justify-content: flex-end;

    padding-right: 73px;
    padding-top: 55px;

    & svg {
      fill: rgba(72, 72, 72, 0.2);
    }
  }
}
